* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.banner-image {
  position: relative;
  height: 800px;
  background-image: url(./public/gambar-pantai-gigi-hiu-11@2x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  opacity: 0;
  transition: opacity 1s;
}

.banner-image.active {
  opacity: 1;
}

